/* === "AURORA" BLACK & RED THEME - RESPONSIVE FINAL === */
:root {
  --aurora-bg: #000000;
  --aurora-surface: #121212;
  --aurora-text-primary: #f0f0f5;
  --aurora-text-secondary: #a0a0a0;
  --aurora-border: rgba(255, 255, 255, 0.1);
  --aurora-glow-start: #e52d27; 
  --aurora-glow-end: #b31217;  
}

/* --- Global & Body --- */
html, body { margin: 0; padding: 0; height: 100%; }
*, *::before, *::after { box-sizing: border-box; }
body {
  color: var(--aurora-text-primary);
  background-image: url('../public/spiderman-bg.png');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow-x: hidden;
}
body.no-scroll { overflow: hidden; }

/* --- Base Layout --- */
.aurora-layout { display: flex; height: 100vh; position: relative; }

/* --- Loading & Error Screens --- */
.loading-screen { display: flex; justify-content: center; align-items: center; height: 100vh; font-size: 1.5rem; }
.error-screen { flex-direction: column; gap: 1rem; color: #ff6b6b; }
.error-screen button { background: #e53935; color: white; border: none; padding: 0.5rem 1rem; border-radius: 6px; cursor: pointer; }

/* --- Sidebar (Mobile First) --- */
.sidebar { position: fixed; top: 0; left: 0; height: 100%; width: 280px; background-color: rgba(10, 10, 15, 0.8); backdrop-filter: blur(15px); border-right: 1px solid var(--aurora-border); display: flex; flex-direction: column; padding: 1rem; transition: transform 0.3s ease-in-out; transform: translateX(-100%); z-index: 2000; flex-shrink: 0; }
.aurora-layout.sidebar-is-open .sidebar { transform: translateX(0); }
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); 
  z-index: 999; 

  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0s 0.3s; 
}
.sidebar-is-open .sidebar-overlay {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease;
}



/* --- Sidebar Content --- */
.sidebar__header { font-size: 1.5rem; font-weight: 600; padding: 0.75rem; margin-bottom: 1rem; text-align: center; color: #fff; }
.sidebar__search-container { position: relative; margin-bottom: 2rem; }
.sidebar__search-container .fa-search { position: absolute; left: 15px; top: 50%; transform: translateY(-50%); color: var(--aurora-text-secondary); }
.sidebar__search-input { width: 100%; padding: 0.75rem 1rem 0.75rem 2.5rem; background-color: rgba(0, 0, 0, 0.2); border-radius: 8px; border: 1px solid var(--aurora-border); color: var(--aurora-text-primary); font-size: 0.9rem; transition: all 0.2s ease; }
.sidebar__search-input:focus { outline: none; background-color: rgba(0, 0, 0, 0.3); border-color: var(--aurora-glow-end); box-shadow: 0 0 0 3px rgba(229, 45, 39, 0.2); }
.sidebar__section { padding: 0 0.5rem; margin-bottom: 0.5rem; display: flex; justify-content: space-between; align-items: center; font-size: 0.8rem; font-weight: 600; text-transform: uppercase; color: var(--aurora-text-secondary); }
.folder-list { list-style: none; padding: 0; margin: 0; flex-grow: 1; overflow-y: auto; }
.folder-list__item { display: flex; align-items: center; gap: 0.85rem; padding: 0.8rem 0.75rem; border-radius: 8px; cursor: pointer; transition: background-color 0.2s ease, color 0.2s ease; }
.folder-list__item span { flex-grow: 1; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.folder-list__item:hover { background-color: rgba(255, 255, 255, 0.05); }
.folder-list__item--active { background: linear-gradient(90deg, var(--aurora-glow-start), var(--aurora-glow-end)); color: white !important; font-weight: 600; box-shadow: 0 4px 15px -5px var(--aurora-glow-end); }
.folder-item__actions { display: flex; align-items: center; margin-left: auto; opacity: 0; transition: opacity 0.2s ease; }
.folder-list__item:hover .folder-item__actions { opacity: 1; }
.btn-icon-edit, .btn-icon-delete { background: transparent; border: none; color: var(--aurora-text-secondary); padding: 0.5rem; border-radius: 6px; cursor: pointer; transition: all 0.2s ease; }
.btn-icon-edit:hover { color: #fff; background-color: rgba(59, 130, 246, 0.2); }
.btn-icon-delete:hover { color: #fff; background-color: rgba(239, 68, 68, 0.2); }

/* --- Sidebar Footer (Updated for Vertical Layout) --- */
.sidebar__footer {
  margin-top: auto;        
  padding-top: 1rem;       
  display: flex;                
  flex-direction: column;      
  gap: 1rem;                 
}

.user-info { display: flex; align-items: center; gap: 0.75rem; max-width: 80%; }
.user-avatar { width: 36px; height: 36px; border-radius: 50%; object-fit: cover; }
.user-email { font-size: 0.8rem; color: var(--aurora-text-secondary); white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }

/* --- Custom, Shorter, Auto-Hiding Scrollbar for BOTH Panels --- */
.folder-list::-webkit-scrollbar,
.main-content::-webkit-scrollbar,
.modal-body::-webkit-scrollbar,
.card-field__value::-webkit-scrollbar,
.controls::-webkit-scrollbar { 
  height: 8px; 
  width: 8px;
}

.folder-list::-webkit-scrollbar-track,
.main-content::-webkit-scrollbar-track,
.modal-body::-webkit-scrollbar-track,
.card-field__value::-webkit-scrollbar-track,
.controls::-webkit-scrollbar-track { 
  background: transparent;
}

.folder-list::-webkit-scrollbar-thumb,
.main-content::-webkit-scrollbar-thumb,
.modal-body::-webkit-scrollbar-thumb,
.card-field__value::-webkit-scrollbar-thumb,
.controls::-webkit-scrollbar-thumb { 
  background-color: transparent;
  border-radius: 20px;
  border: 3px solid transparent;
  background-clip: content-box;
}

.folder-list:hover::-webkit-scrollbar-thumb,
.main-content:hover::-webkit-scrollbar-thumb,
.modal-body:hover::-webkit-scrollbar-thumb,
.card-field__value:hover::-webkit-scrollbar-thumb,
.controls:hover::-webkit-scrollbar-thumb { 
  background-color: #555; 
}

/* --- Main Content --- */
.main-content {
  width: 100%;
  padding: 1.5rem;          
  margin: 1.5rem;           
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  background-color: rgba(10, 10, 15, .8);
  border-radius: 12px;
}

/* --- START: MODIFIED HEADER LAYOUT --- */
.main-content__header {
  display: flex;
  align-items: center;
  flex-wrap: wrap; /* Key for mobile responsiveness */
  gap: 1rem;
  margin-bottom: 2rem;
}

.sidebar-toggle {
  display: block;
  background: rgba(10, 10, 15, 0.6);
  border: 1px solid var(--aurora-border);
  color: var(--aurora-text-primary);
  padding: 0.5rem 0.7rem;
  border-radius: 8px;
  cursor: pointer;
  order: 1; /* First on mobile */
}

.main-content__title {
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
  
  /* Mobile layout */
  font-size: 1.8rem;
  order: 2; /* Second on mobile */
  flex-grow: 1; /* Takes up space next to burger */
}

.main-search-container {
  position: relative;
  /* Mobile layout: full width on its own row */
  order: 4;
  width: 100%; 
}

.controls {
  display: flex;
  gap: 0.5rem;
  background-color: rgba(18, 18, 18, 0.6);
  padding: 0.5rem;
  border-radius: 12px;
  border: 1px solid var(--aurora-border);
  backdrop-filter: blur(10px);
  flex-wrap: nowrap;
  overflow-x: auto;
  /* Mobile layout FIX */
  order: 3;
  width: 100%; /* Ensure container takes full width on its line */
}

/* Tablet and larger screens */
@media (min-width: 992px) {
  .main-content__header {
    flex-wrap: nowrap; /* Prevent wrapping */
    gap: 1.5rem;
  }
  .main-content__title {
    order: 2;
    flex-grow: 0; 
    font-size: 2.2rem;
    margin-right: auto; /* ADDED: Pushes subsequent items to the end */
  }
  .main-search-container {
    order: 3;
    flex-grow: 0; /* MODIFIED: No longer needs to grow */
    width: auto;
    max-width: 450px;
  }
  .controls {
    order: 4;
    width: auto; /* Revert to shrink-to-fit content */
    overflow-x: visible;
  }
}

@media (min-width: 1024px) {
  .main-content__title {
    font-size: 2.5rem;
  }
}

/* --- Main Content Search Bar --- */
.main-search-container .fa-search {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--aurora-text-secondary);
}
.main-search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border: 1px solid var(--aurora-border);
  color: var(--aurora-text-primary);
  font-size: 0.9rem;
  transition: all 0.2s ease;
}
.main-search-input:focus {
  outline: none;
  background-color: rgba(0, 0, 0, 0.3);
  border-color: var(--aurora-glow-end);
  box-shadow: 0 0 0 3px rgba(229, 45, 39, 0.2);
}
/* --- END: MODIFIED HEADER LAYOUT --- */


.control-btn-primary {
  background: #e0e0e0;
  color: #121212;
  border: none;
  padding: 0.5rem 0.75rem; 
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.2s ease;
  flex-shrink: 0; /* Prevents this button from shrinking */
}

.control-btn-primary:hover {
  background: #fff;
}
.control-btn-primary:disabled {
  background: #555;
  color: #888;
  cursor: not-allowed;
}

.control-group {
  display: flex;
  flex-shrink: 0; /* Prevents button groups from shrinking */
}

.control-btn {
  background: none;
  border: 1px solid transparent;
  color: #aaa;
  padding: 0.5rem; 
  cursor: pointer;
  transition: all 0.2s ease;
}
.control-group .control-btn:first-child {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
.control-group .control-btn:last-child {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.control-btn:hover {
  color: #fff;
  background: rgba(255, 255, 255, 0.05);
}
.control-btn.active {
  background: #e0e0e0;
  color: #121212;
}

/* --- Grid & Cards --- */
.grid-container { display: grid; gap: 1.5rem; }
.grid-size-small { grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); }
.grid-size-medium { grid-template-columns: repeat(auto-fill, minmax(350px, 1fr)); }
.grid-size-large { grid-template-columns: repeat(auto-fill, minmax(450px, 1fr)); }
.list-container .smart-card { max-width: 100%; } /* Ensure list view items take full width */

.no-bookmarks { display: flex; flex-direction: column; align-items: center; justify-content: center; text-align: center; color: var(--aurora-text-secondary); padding: 4rem; flex-grow: 1; }
.no-bookmarks .fa-book-dead { font-size: 3rem; margin-bottom: 1rem; opacity: 0.5; }

/* --- Smart Card --- */
.smart-card {
  background-color: rgba(18, 18, 18, 0.6);
  backdrop-filter: blur(12px);
  border: 1px solid var(--aurora-border);
  border-radius: 12px;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 0px rgba(255, 0, 0, 0); /* invisible initially */
}

.smart-card:hover {
  background-color: rgba(255, 255, 255, 0.05); /* subtle red overlay */
  border-color: rgba(255, 255, 255, 0.295);
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.3); /* glowing red border */
}


.smart-card:hover { transform: translateY(-3px); box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3); }
.smart-card__header h5 { margin: 0; font-size: 1.1rem; font-weight: 600; color: #e5e5e5; }
.smart-card__body { position: relative; background-color: rgba(0, 0, 0, 0.2); padding: 1rem; border-radius: 8px; }
.smart-card__body pre { margin: 0; white-space: pre-wrap; word-wrap: break-word; font-family: 'Fira Code', 'Courier New', monospace; font-size: 0.85rem; color: #cccccc; line-height: 1.6; }

.btn-icon-copy {
    color: #888;
    cursor: pointer;
    background: none;
    border: none;
    padding: .5rem;
    font-size: .9rem;
    transition: color .2s;
    position: absolute;
    top: .5rem; 
    right: .5rem;
    z-index: 1;
}

.btn-icon-copy:hover { color: #fff; }
.smart-card__footer { display: flex; justify-content: space-between; align-items: center; border-top: 1px solid var(--aurora-border); padding-top: 1rem; }
.smart-card__link-info { display: flex; align-items: center; gap: 0.6rem; font-size: 0.85rem; color: #999; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; }
.smart-card__actions { display: flex; align-items: center; gap: 0.75rem; flex-shrink: 0; }
.btn-icon-edit-card { background-color: #e0e0e0; color: #212121; border: none; border-radius: 10px; width: 40px; height: 40px; display: flex; justify-content: center; align-items: center; font-size: 1rem; cursor: pointer; box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4); transition: all 0.2s ease-in-out; }
.btn-icon-edit-card:hover { transform: translateY(-2px); box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5); background-color: #ffffff; }
.btn-icon-hover { background: none; border: none; color: #888; font-size: 1.1rem; padding: 0.5rem; cursor: pointer; text-decoration: none; opacity: 0; transform: scale(0.8); transition: all 0.2s ease-in-out; }
.smart-card:hover .btn-icon-hover { opacity: 1; transform: scale(1); }
.btn-icon-hover:hover { color: #fff; }
.btn-icon-delete-hover:hover { color: #e53935; }

/* === NEW DYNAMIC SMART CARD === */
.smart-card { background-color: rgba(18, 18, 18, 0.7);  margin-bottom: 0.5rem;
  margin-top: 0.75rem;   backdrop-filter: blur(15px); border: 1px solid rgba(255, 255, 255, 0.1); border-radius: 12px; display: flex; flex-direction: column; transition: all 0.2s ease-in-out; }
.smart-card:hover { transform: translateY(-3px); box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3); border-color: rgba(255, 255, 255, 0.15); }
.smart-card__header { padding: 1.25rem; }
.smart-card__header h5 { margin: 0 0 0.25rem 0; font-size: 1.1rem; font-weight: 600; color: #e5e5e5; }
.smart-card__header p { margin: 0; font-size: 0.9rem; color: #a0a0a0; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.smart-card__body { display: flex; flex-direction: column; gap: 1rem; padding: 0 1.25rem 1.25rem 1.25rem; }
.card-field { display: flex; flex-direction: column; gap: 0.5rem; }


.card-field__title {
  font-size: 0.75rem;
  text-transform: uppercase;
  color: #888;
  font-weight: 600;
  overflow-wrap: break-word;
  min-width: 0;
   margin-top: 1rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1; 
  -webkit-box-orient: vertical;
  word-break: break-all; 
}

.card-field__value { 
  position: relative;
  background-color: rgba(0, 0, 0, 0.3); 
  padding: 0.75rem; 
  border-radius: 8px; 
}

.card-field__value pre { 
  margin: 0; 
  white-space: pre-wrap; 
  overflow-wrap: break-word; 
  font-family: 'Fira Code', 'Courier New', monospace; 
  font-size: 0.9rem; 
  color: #cccccc; 
  line-height: 1.6; 
}

/* --- The copy button is styled to be absolute inside its parent --- */
/* Note: The main .btn-icon-copy rule was already modified above, this is for context */
.btn-icon-copy:hover { color: #fff; }
.smart-card__footer { display: flex; justify-content: flex-end; align-items: center; border-top: 1px solid rgba(255, 255, 255, 0.1); padding: 0.75rem 1.25rem; }
.smart-card__actions { display: flex; align-items: center; gap: 0.75rem; }
.btn-icon-edit-card { background-color: #e0e0e0; color: #212121; border: none; border-radius: 10px; width: 40px; height: 40px; display: flex; justify-content: center; align-items: center; font-size: 1rem; cursor: pointer; box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4); transition: all 0.2s ease-in-out; }
.btn-icon-edit-card:hover { transform: translateY(-2px); box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5); background-color: #ffffff; }
.btn-icon-hover { background: none; border: none; color: #888; font-size: 1.1rem; padding: 0.5rem; cursor: pointer; text-decoration: none; opacity: 0; transform: scale(0.8); transition: all 0.2s ease-in-out; }
.smart-card:hover .btn-icon-hover { opacity: 1; transform: scale(1); }
.btn-icon-hover:hover { color: #fff; }
.btn-icon-delete-hover:hover { color: #e53935; }

/* === NEW DYNAMIC MODAL === */
.modal-overlay { position: fixed; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.75); display: flex; justify-content: center; align-items: center; z-index: 3000; backdrop-filter: blur(5px); padding: 1rem; }
.modal-content { background-color: #1f1f1f; padding: 1.5rem; border-radius: 12px; width: 100%; max-width: 600px; border: 1px solid #333; box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5); }
.modal-content h3 { margin: 0 0 1.5rem 0; font-size: 1.2rem; }
.modal-body { display: flex; flex-direction: column; gap: 1rem; max-height: 60vh; overflow-y: auto; padding-right: 0.5rem; }
.modal-field-row { display: flex; align-items: flex-start; gap: 0.75rem; }
.modal-input--title { flex: 0 0 150px; }
.modal-textarea { flex: 1 1 auto; resize: vertical; min-height: 42px; line-height: 1.5; padding: 0.75rem; border-radius: 6px; }
.modal-input, .modal-textarea { background-color: #333; border: 1px solid #555; color: #fff; font-size: 0.9rem; transition: all 0.2s ease; font-family: inherit; }
.modal-input:focus, .modal-textarea:focus { outline: none; border-color: var(--aurora-glow-end); background-color: #444; }
.modal-remove-field-btn { background: none; border: none; color: #888; cursor: pointer; padding: 0.5rem; font-size: 1rem; align-self: center; }
.modal-remove-field-btn:hover { color: var(--aurora-glow-start); }
.modal-add-field-btn { background: rgba(255, 255, 255, 0.1); border: 1px dashed #777; color: #ccc; padding: 0.75rem; border-radius: 8px; cursor: pointer; transition: all 0.2s ease; margin-top: 0.5rem; }
.modal-add-field-btn:hover { background: rgba(255, 255, 255, 0.15); border-color: #999; }
.modal-footer { margin-top: 1.5rem; display: flex; justify-content: flex-end; gap: 1rem; border-top: 1px solid #444; padding-top: 1.5rem; }
.modal-save-btn, .modal-cancel-btn { border: none; padding: 0.7rem 1.2rem; border-radius: 8px; cursor: pointer; font-weight: 600; font-size: 0.9rem; transition: all 0.2s ease; }
.modal-save-btn { background-color: var(--aurora-glow-start); color: white; }
.modal-save-btn:hover { background-color: var(--aurora-glow-end); }
.modal-cancel-btn { background-color: #424242; color: #e0e0e0; }
.modal-cancel-btn:hover { background-color: #616161; }


/* --- Desktop Styles --- */
@media (min-width: 1024px) {
  .sidebar { position: static; transform: translateX(0); height: auto; margin: 1rem; border-radius: 1rem; z-index: 1; }
  .sidebar-toggle, .sidebar-overlay { display: none; }
  .main-content { padding: 2rem 3rem; }
}


/* --- Responsive Sidebar Drawer for Mobile (screens smaller than 1024px) --- */
@media (max-width: 1023px) {
  .sidebar {
    position: fixed; 
    left: 0;
    top: 0;
    height: 100%;
    z-index: 1000; 
    transform: translateX(-100%); 
    transition: transform 0.3s ease-in-out;
  }
  .aurora-layout.sidebar-is-open .sidebar {
    transform: translateX(0);
  }
}


/* --- login page Styles --- */
.login-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}


@keyframes float {
  0% { transform: translate(0, 0) scale(1); }
  100% { transform: translate(100px, 50px) scale(1.1); }
}

.login-content {
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-card {
  position: relative;
  background: rgba(18, 18, 18, 0.664);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border-radius: 1.25rem;
  padding: 2rem;
  width: 100%;
  max-width: 400px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1.25rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}



.login-card__icon {
  font-size: 2rem;
  color: var(--aurora-text-primary);
  margin-bottom: 0.5rem;
}

.login-card__title {
  font-size: 2.25rem;
  font-weight: 600;
  margin: 0;
}

.login-card__subtitle {
  font-size: 0.95rem;
  color: var(--aurora-text-secondary);
  line-height: 1.6;
  margin: 0;
  max-width: 90%;
}

.google-button-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 1rem;
}

.signed-in-as {
  font-size: 0.85rem;
  color: var(--aurora-text-secondary);
  margin-bottom: 1rem;
}

.continue-btn {
  width: 100%;
  padding: 0.8rem 1.5rem;
  font-weight: 600;
  color: white;
  background: linear-gradient(90deg, var(--aurora-glow-start), var(--aurora-glow-end));
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(229, 45, 39, 0.3);
}

.continue-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(229, 45, 39, 0.4);
}

.continue-btn:active {
  transform: translateY(0);
}

@media (max-width: 480px) {
  .login-card {
    padding: 1.5rem 1rem;
  }

  .login-card__title {
    font-size: 1.6rem;
  }

  .login-card__subtitle {
    font-size: 0.85rem;
  }

  .continue-btn {
    font-size: 0.9rem;
