/* ---------------------------full screen */

/* --- New Styles for Fullscreen Feature --- */

/* Make the modal title a positioning context for the button */
.modal-title {
  position: relative;
  padding-right: 40px; /* Make space for the button */
}

/* Style the fullscreen button */
.modal-fullscreen-btn {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: #a0aec0; /* Light grey */
  cursor: pointer;
  font-size: 1rem;
  padding: 8px;
  border-radius: 50%;
  line-height: 1;
  transition: all 0.2s ease;
}

.modal-fullscreen-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

/* Styles for when the viewer IS in fullscreen mode */
.file-viewer-modal:fullscreen {
  background-color: #000; /* Use a solid black background */
  padding: 0;
  border: none;
  border-radius: 0;
  width: 100%;
  height: 100%;
}

.file-viewer-modal:fullscreen .modal-title {
  background-color: rgba(20, 20, 20, 0.8);
  border-radius: 0;
  position: absolute; /* Float title over content */
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.file-viewer-modal:fullscreen .modal-body {
  height: 100%;
  max-height: 100vh;
}

.file-viewer-modal:fullscreen .modal-footer {
  display: none; /* Hide the 'Close' button in fullscreen, user can press Esc */
}

/* Ensure images don't stretch and distort in fullscreen */
.file-viewer-modal:fullscreen .file-viewer-body img {
  object-fit: contain; /* This is crucial! */
}


